<template>
    <div id="areaTree">
        <div class="tree-box">
            <div class="zTreeDemoBackground left">
                <ul id="treeDemo" class="ztree"></ul>
            </div>
        </div>
    </div>
</template>

<script>

    import '../../../plugins/zTree/jquery-1.4.4.min.js'
    import '../../../plugins/zTree/jquery.ztree.core.js'
    import '../../../plugins/zTree/jquery.ztree.excheck.js'
    import '../../../plugins/zTree/jquery.ztree.exedit.js'
    import {getUrlKey} from "@/util/project-submit.js";

    export default {
      name: 'areaTree',
      components:{

      },
      data:function(){
        return{
            url_id:'',
            setting:{
              data:{
                key: {
                  children: "childrenList",
                  name: "itemName"
		            }
              }
            },
            zNodes:[{ "id":1, "itemName":"test1",
                      childrenList: [
                        { "id":3, "itemName":"test3"},
                        { "id":4, "itemName":"test4"},
                        { "id":5, "itemName":"test5"}
                      ]
                   }]
        }
      },
      created(){
          this.url_id = getUrlKey("keyValues")
          // this.url_id ="11551"
          if(this.url_id){
            this.updataTree()
          }

      },
      methods:{
        updataTree(){//tree-data
            const _self = this
            const pageType = "3"
            const t = new Date().getTime();
            const Url = `/ei-web/attaItemList?projOrFundId=${this.url_id}&itemType=${pageType}&t=${t}`
            this.$axios({
              url:Url,
              method: "get"
            }).then(function(response) {
              if (response.data.status === "0" ) {
                  console.log("data>>>>>>>>>>>>>>>>>>>>>>>")
                  console.log(response.data.data)
                  // _self.setting.data.key.children = "childrenList"
                  // _self.setting.data.key.name = "itemName"
                //  var dd =
                //   _self.zNodes = dd
              }
            })
        },
        freshArea:function(){
            $.fn.zTree.init($("#treeDemo"), this.setting, this.zNodes);
        }
      },
      mounted(){
        $.fn.zTree.init($("#treeDemo"), this.setting, this.zNodes);
      }
    }
</script>
<style>

    #areaTree{
        border:1px solid #e5e5e5;    margin-bottom: 2px;border-radius: 4px;overflow: hidden;
    }
    .box-title{
            border-radius: 3px 3px 0 0;background-color: #f5f5f5;
    }
    .box-title a{
            color: #2fa4e7;
             text-decoration: none;font-size:14px;    display: block;
    padding: 8px 15px;cursor: pointer;
    }
    .box-title .fa{
        float:right;line-height: 20px;
    }
</style>



