              }

              if(dataLength!==0){
                  this.processinstanceIdA = response.data.data[0].processinstanceId; //instId
                  this.processdefinitionIdA = response.data.data[0].processdefinitionId; //defid
                  this.taskinstanceIdA = response.data.data[dataLength - 1].taskinstanceId; //taskId
              }

              this.flowChartData = response.data.data;
            }
          })

    },
    getProjDecisionInfo(){//-
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionInfo + `/${this.url_project_id}?t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                if(response.data.data.model){
                    this.investmentContentData = response.data.data.model
                }

              }
            })

    },
    getProjDecisionVoteList(){//-
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionVoteList + `?projId=${this.url_project_id}&t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                this.ProjAppVoteListTableDataA = response.data.data;
              }
            })

    },
    getProjDecisionVoteMagInfo(){//-
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionVoteMagInfo + `/${this.url_project_id}?t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                this.projAppVoteMagInfoDataA = response.data.data.model;
              }
            })

    },
    getProjDecisionFixflowTaskList(){//-
        var t = new Date().getTime();
        this.$axios({
          url:
            url.getProjDecisionFixflowTaskList + `?projId=${this.url_project_id}&t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              let dataLength = response.data.data.length;

              if(dataLength===0){
                this.graphMoelD = false
              }
              if(dataLength!==0){
                  this.processinstanceId = response.data.data[0].processinstanceId; //instId
                  this.processdefinitionId = response.data.data[0].processdefinitionId; //defid
                  this.taskinstanceId =response.data.data[dataLength - 1].taskinstanceId; //taskId
              }

              this.flowChartDataA = response.data.data;
            }
          })

    },
    add_report_project(){//-
      var _self = this

      // projObject
      if(this.rptId!==''&&this.rptId!==null){
         var url = appendParams(
          `/eiweb/action/bizObjectBase.action`,
          {
            keyValues :this.rptId,
            tableName : "TZ_T_DUE_DILIGENCE_RPT",
            dealMark : "update",
            sequenceName : "SEQ_DUE_DILIGENCE_RPT",
            springJndiId: "YHKG_DS_EI",
            resultJsp : "../WEB-INF/views/dueDiligenceSurvey/addDueDiligenceSurvey.jsp",
            className : "com.founder.ei.bizobject.DueDiligenceSurveyObj",
            projectId: this.url_project_id,
            PROJ_OBJECT:this.projObject,
            openMethod:"2"
          }
        );
        layerOpenWindow("", url, '1000px', '600px', _self.getReportDetail)
      }
      //
      if(this.rptId==null){
          var url = appendParams(
          `/eiweb/action/bizObjectBase.action`,
          {
            keyValues :'',
            tableName : "TZ_T_DUE_DILIGENCE_RPT",
            dealMark : "new",
            sequenceName : "SEQ_DUE_DILIGENCE_RPT",
            springJndiId: "YHKG_DS_EI",
            resultJsp : "../WEB-INF/views/dueDiligenceSurvey/addDueDiligenceSurvey.jsp",
            className : "com.founder.ei.bizobject.DueDiligenceSurveyObj",
            projectId: this.url_project_id,
            PROJ_OBJECT:this.projObject,
            openMethod:"2"
          }
        );
        layerOpenWindow("", url, '1000px', '600px', _self.getReportDetail)
      }

    },
    getReportDetail(){
        var t = new Date().getTime();

        this.$axios({//

              url: url.getDueDiligenceRpt + `/${this.url_project_id}?t=${t}`,
              method: "get"
            })
              .then(response => {
                if (response.data.status === '0') {
                  this.surveyReport = response.data.data.model
                  this.rptId = response.data.data.model.rptId
                }
              })
    },
    click_project(){//
      var url = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues : this.url_project_id,
          tableName : "TZ_T_PROJ_INFO",
          dealMark : "update",
          sequenceName : "",
          springJndiId: "YHKG_DS_EI",
          resultJsp : "../WEB-INF/views/funds/updateProjectMsgTogether.jsp",
          className : "com.founder.ei.bizobject.FundsInvestObj",
          frameInd: "2",
          openMethod:"2"
        }
      );
      layerOpenWindow("", url, '1000px', '600px',this.getProjAppEvent);

    },
    click_jue_pro(){//
        var url = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues : this.url_project_id,
          tableName : "TZ_T_PROJ_INFO",
          dealMark : "update",
          sequenceName : "",
          springJndiId: "YHKG_DS_EI",
          resultJsp : "../WEB-INF/views/funds/updateProjectDecisionTogether.jsp",
          className : "com.founder.ei.bizobject.FundInvestDecisonObj",
          frameInd: "0",
          openMethod:"2"
        }
      );

       layerOpenWindow("", url, '1000px', '600px',this.newProjectEvent);
    },
    getProjAppEvent(){//-
      //projStatus -
      this.getProjInfoDetail()
      //
      this.getProjAppInfo()//
      this.getProjAppVoteList() //
      this.getProjAppVoteMagInfo()//
      this.getProjAppFixflowTaskList()//
    },
    newProjectEvent(){//-
      //projStatus -
      this.getProjInfoDetail()
      //
      this.getProjDecisionInfo()//-
      this.getProjDecisionVoteList()//-
      this.getProjDecisionVoteMagInfo()//-
      this.getProjDecisionFixflowTaskList()//-

    },
    btnSubmit() {
      editProjInfoTogether();
    },
    //open
    openFlowSubmit(val) {
      //
      this.dialogTableVisible = true;
      if(val==1){
        //url 
        this.graphUrl = appendParams(
          "/sysweb/action/workflowProcess!flowGraphics.action",
          {
            defId: this.processdefinitionIdA,
            instId: this.processinstanceIdA,
            taskId: this.taskinstanceIdA
          })

      }

      if(val==2){
        //url 
        this.graphUrl = appendParams(
          "/sysweb/action/workflowProcess!flowGraphics.action",
          {
            defId: this.processdefinitionId,
            instId: this.processinstanceId,
            taskId: this.taskinstanceId
          })

      }
      layerOpenWindow("", this.graphUrl, '800px', '600px', function () {});

    },

  }
};

</script>


