
<template>
    <div>
        <v-table :tableDataContent="tableData" :tableOptions="tableOptions" :tindex="0" :selectionCheck="1" :tagsModel="true" :projId="projIdA" :keyValuesType="keyValuesType" @delete-search="deleteSearchClick"></v-table>
        <!-- pagination -->
        <div class="paginationNav paginationCommon">
            <div class="title">  {{recordsTotal}}   {{totalPage}} </div>
            <el-button  @click="indexBtn"></el-button>
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            @prev-click="prevClick"
            @next-click="nextClick"
            :current-page="currentPage"
            :total="recordsTotal"
            :page-size="pageSize"
            layout="prev, pager, next"
            background
            prev-text=''
            next-text=''
            >
            </el-pagination>
            <el-button @click="backBtn"></el-button>
       </div>
    </div>
</template>

<script>
import TableCom from '@/components/tableCom'
import url      from '@/serviceAPI.config.js'
export default {
  props:{
    projId:{
      type:String,
      default:''
    },
    tableOptions:{
      type:Array,
      default:[]
    },
    ajaxUrl:{
      type:String,
      default:''
    },
    tindex:{
      type:Boolean
    },
    selectionCheck:{
      type:Boolean
    },
    flag:{
      type:Boolean,
      default:false
    }
  },
  components: {
    "v-table": TableCom
  },
  //
  data() {
    return {
      keyValuesType:2,
      tableData:[],    //table
      //
      currentPage:1,   //
      pageSize:5,      //5
      totalPage:0,     //
      shouYePage:1,    //
      recordsTotal:0,  //
      //
      newOject:{
        tableName:'TZ_T_PROJ_INFO',
        dealMark:'view',
        sequenceName:'',
        springJndiId:'YHKG_DS_EI',
        resultJsp:'../WEB-INF/views/funds/MFInvestCFInfomation.jsp',
        className:'com.founder.ei.bizobject.MFInvestCFInfomationObj',
      },
       //
      editOject:{
        tableName:'TZ_T_TERM_SHEET',
        dealMark:'update',
        sequenceName:'SEQ_TERM_SHEET',
        springJndiId:'YHKG_DS_EI',
        resultJsp:'../WEB-INF/views/fundsProjectDecision/updatePreInvestFiles.jsp',
        className:'com.founder.ei.bizobject.PreInvestFilesObj',
      },
      projIdA:"",
    }
  },
  created(){
     this.projIdA = this.projId
  },
  watch: {
     flag(val){
        if(val){
           this.getEnteInfos(this.projId,this.pageSize, this.currentPage)
        }
     }
  },
  methods: {
    deleteSearchClick(val) {

      if(val==="1"){//-
        this.getEnteInfos(this.projId,this.pageSize, this.currentPage)
      }

      if(val==="2"){//
        this.getEnteInfos(this.projId,this.pageSize, 1)
      }

    },
    getEnteInfos(projId,pageSize,currentPage){
        let _self = this
        var t = new Date().getTime();
        _self.$axios({
        url:`${this.ajaxUrl}?projId=${projId}&pageSize=${pageSize}&currPage=${currentPage}&t=${t}`,
        method: "get"
        })
        .then(response => {
            if(response.status===200){
                _self.tableData = response.data.data//table
                _self.recordsTotal = response.data.totalCount//
                _self.totalPage = response.data.totalPage//
            }
        })
        .catch(error => {
          console.log(error)
        })
    },
      // 
    indexBtn() {
      this.currentPage = this.shouYePage;
      this.getEnteInfos(this.projId, this.pageSize, this.currentPage);
    },
    // 
    backBtn() {
      this.currentPage = this.totalPage;
      this.getEnteInfos(this.projId, this.pageSize, this.currentPage);
    },
    //
    prevClick(currentPage) {
      this.currentPage = currentPage;
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
    },
    //
    nextClick(currentPage) {
      this.currentPage = currentPage;
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
    },
    handleSizeChange(size) {
      //pageSize 
      let _self = this;
      _self.pageSize = size; //
      console.log(` ${size} `);
    },
    handleCurrentChange(currentPage) {
      //currentPage   
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
      this.currentPage = currentPage; //
    }
  },
};
</script>

<style>
/*-start*/
.paginationCommon {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
.paginationCommon .title {
  font-size: 12px;
  font-family: "Microsoft YaHei";
  margin-right: 106px;
}
/*button  */
.paginationCommon .el-button {
  padding: 0 10px;
  display: inline-block;
  font-size: 13px;
  min-width: 29px;
  height: 29px;
  line-height: 29px;
  vertical-align: top;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
  color: #d1ab62;
  border: 1px solid #d1ab62;
}

paginationCommon .el-button:hover {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:active {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:link {
  color: #fff;
  background-color: #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/prev_sanjiao.png) no-repeat scroll 7px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-next,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/next_sanjiao.png) no-repeat scroll 50px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination button,
.el-pagination span:not([class*="suffix"]) {
  padding: 0 10px;
}

.paginationCommon .el-pagination.is-background .el-pager li:not(.disabled).active {
  color: #fff;
  background-color: #d1ab62;
}
/*-end*/
</style>





