<script>
import Scroll from './scroll'
import axios from "axios";
import {getUrlKey,layerOpenWindow,appendParams} from "@/util/project-submit.js";
export default {
  props:{
    contextPath: { //
      type: String,
      default: "/ei-web/attaItemList"
    },
    flag:{
      type:Boolean,
      default:false
    }
  },
  components: {
    Scroll
  },
  data() {
    return {
      imgFolder:require('@/assets/images/file.png'),
      imgFile:require('@/assets/images/file1.png'),
      imgFilePng:require('@/assets/images/filePng.png'),
      imgFileJpg:require('@/assets/images/file-jpg.png'),
      imgFileRar:require('@/assets/images/fileRar.png'),
      imgFilePPT:require('@/assets/images/file-ppt-iocn.png'),
      imgWordFile:require('@/assets/images/wordFile.png'),
      imgPDF:require('@/assets/images/filePDF.png'),
      imgXlsx:require('@/assets/images/file-xlsx.png'),
      imgDocx:require('@/assets/images/file-word-iocn.png'),
      imgHtml:require('@/assets/images/fileHtml.png'),
      imgPPTX:require('@/assets/images/file-pptx.png'),
      inputValue:'',
      inputValueEdit:"",
      nodeID:'',
      highLight:true,
      cnodeType:'',
      itemNodeId:"",
      default_nodeId:'',//
      node_parentId:'0',//item-

      //
      node_itemName:"",
      node_itemFundId:"",
      node_itemParentId:"",
      tableData: [],
      treeData:[],
      loading:false,
      defaultProps: {
        children: "childrenList",
        label: "itemName",
      },
      multipleSelection: [],//table-Select
      //
      currentPage: 1, //
      pageSize:10, //5
      totalPage: 0, //
      shouYePage: 1, //
      recordsTotal: 0, //
      url_id:''
    };
  },
  created() {
    this.url_id = getUrlKey("keyValues")
  },
  watch: {
    flag(val){
      if(val){
          this.updataTree()
      }
    }
  },
  methods: {
    updataTree(){//tree-data
      const _self = this
      const pageType = "3"
      const t = new Date().getTime();
      const Url = `${this.contextPath}?projOrFundId=${this.url_id}&itemType=${pageType}&t=${t}`
      axios({
        url:Url,
        method: "get"
      }).then(function(response) {
        if (response.data.status === "0" ) {

          const arrFirst = response.data.data[0].childrenList

          _self.itemNodeId = arrFirst[0].itemId
          const default_nodeId = arrFirst[0].itemId

          let nodeLight = _self.$refs.tree.getCurrentKey()
          if(nodeLight==null){
            _self.getTableData(default_nodeId)
          }else{
            _self.getTableData(nodeLight)
          }
          //tree
          _self.treeData = response.data.data

        }
      })
    },
    handleSelectionChange(val) {//check
      this.multipleSelection = val;
    },
    add_new(){//
      const _self = this;
      const keyValues = "";
      const pageType = "3"
      const newPageUrl = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues: keyValues,
          tableName: "BD_T_FUND_ATTA",
          dealMark: "new",
          sequenceName: "SEQ_BD_T_FUND_ATTA",
          springJndiId: "YHKG_DS_EI",
          resultJsp:"../WEB-INF/views/financingManage/dealFundAtta.jsp",
          className: "com.founder.ei.bizobject.FundAttaObj",
          fundId: this.url_id,
          openMethod: "2",
          nodeId:this.itemNodeId,
          itemType:pageType
        }
      );
      layerOpenWindow("",newPageUrl,'600px', '350px',function(){
        _self.updataTree()
      })
    },
    edit_click(){//
      const _self = this;

      if (this.multipleSelection.length !=1) {
        layer.msg(``,{icon:6})
      } else if(this.multipleSelection[0].editable=="1"){
        layer.msg(`,`,{icon:6})
      }else{
              const id = this.multipleSelection[0].attaId;
              const editUrl = appendParams(
                `/eiweb/action/bizObjectBase.action`,
                {
                  keyValues: id,
                  tableName: "BD_T_FUND_ATTA",
                  dealMark: "update",
                  sequenceName: "",
                  springJndiId: "YHKG_DS_EI",
                  resultJsp:"../WEB-INF/views/financingManage/dealFundAtta.jsp",
                  className: "com.founder.ei.bizobject.FundAttaObj",
                  openMethod: "2",
                  nodeId:this.itemNodeId,
                  parentId:this.node_parentId
                }
              );
              layerOpenWindow("",editUrl,'600px', '350px',function(){
                _self.getTableData(_self.itemNodeId,_self.pageSize,_self.currentPage)
              })
      }
    },
    batch_download(){//
      const _self = this;
      if (this.multipleSelection.length==0) {
        layer.msg(``,{icon:6})
      } else{
        const arrFileUrl = []
        for(let item of this.multipleSelection){
          const itemObject={
            url:item.attaFileUrl,
            name:item.attaFileName
          }
          arrFileUrl.push(item.attaFileUrl)
        }

        var btn = document.getElementById('download-btn');

        //mp3
        function download(name, href) {
            var a = document.createElement("a"), //a
                e = document.createEvent("MouseEvents"); //
            e.initEvent("click", false, false); //
            a.href = href; //
            a.download = name; //
            a.dispatchEvent(e); //click
        }

        //
        btn.onclick = function name(params) {
            for (let index = 0; index < arrFileUrl.length; index++) {
                download('' + index + '', arrFileUrl[index]);
            }
        }
      }
    },
    operating_record(){//
        const recordUrl = appendParams(
          `/eiweb/action/showListview.action`,
          {
            listViewFileName:"fundAttaDetailView.xml",
            listViewId: "detailListId",
            fromFilter: "FUND_ID='"+this.url_id+"'",
            fundId:this.url_id
          }
        );
        layerOpenWindow("",recordUrl,'1000px', '600px',function(){})
    },
    getTableData(nodeId,pageSize=10, currentPage=1){//table-data
      const t = new Date().getTime();
      this.$axios({
          url: `/ei-web/fileList?length=${pageSize}&currPage=${currentPage}&nodeId=${nodeId}&fundId=${this.url_id}&itemType=3&t=${t}`,
          method: "get"
        })
        .then(response => {
          if (response.status === 200) {

            this.tableData = response.data.data //table
            this.recordsTotal = response.data.totalCount //
            this.totalPage = response.data.totalPage //
            this.loading = false
            let nodeLight = this.$refs.tree.getCurrentKey()
            if(nodeLight==null){
              this.$refs.tree.setCurrentKey(this.itemNodeId)
            }else{
              //
              this.$refs.tree.setCurrentKey(nodeLight)
            }

          }
        })
    },
    query(){//
      const t = new Date().getTime();
      //
      const projId      = this.url_id
      const pageSize    = this.pageSize
      const currentPage = this.currentPage
      const keyWord     = this.inputValue

      const nodeId = this.$refs.tree.getCurrentKey()
      // if(keyWord==''){
      //    return layer.msg("",{icon:6})
      // }
      // this.loading = true
      if(keyWord.length>10){
        return layer.msg("10",{icon:6})
      }

      this.$axios({
        url: `/ei-web/fileList?length=${pageSize}&currPage=${currentPage}&nodeId=${nodeId}&fundId=${projId}&keyWord=${keyWord}&itemType=3&t=${t}`,
        method: "get"
      })
      .then(response => {
        if (response.status === 200) {
          this.tableData = response.data.data //table
          this.recordsTotal = response.data.totalCount //
          this.totalPage = response.data.totalPage //
          this.loading = false
        }
      })
    },
    //input start
    handleBlur(){
      this.nodeID = ''
      this.highLight = true
    },

    //input End
    changeEdit(){//edit
      if(this.inputValueEdit.length>10) return layer.msg("10",{icon:6})
      this.changeNode();
      this.nodeID = ''
      this.highLight = true
    },

     changeEditNew(){//edit
      if(this.inputValueEdit.length>10) return layer.msg("10",{icon:6})
      this.newNode();
      this.nodeID = ''
      this.highLight = true
    },

    changeEditClose(){
      this.nodeID = ''
      this.highLight = true
    },
    handleClear(){//clear
    },
    handleChange(value){

    },
    handleNodeClick(data) {//node
      this.node_parentId =  data.parentId//
      this.itemNodeId   = data.itemId//

      const pageSize    = this.pageSize
      const currentPage = this.currentPage

      this.getTableData(this.itemNodeId,pageSize,currentPage)
    },
    append(data,type) {//
      this.highLight = false
      this.cnodeType = type

      if(type=="1"){
        this.inputValueEdit = data.itemName
        //input
        this.nodeID = data.itemId+"1"
      }

      if(type=="2"){
        this.inputValueEdit = ''
        //input
        this.nodeID = data.itemId+"2"
      }

      //
      this.node_itemFundId = data.projectOrFundId
      this.node_itemParentId = data.itemId
    },
    editChildenNode(){//enter
      if(this.inputValueEdit==""){
        layer.msg("",{icon:6})
      }else{


        if(this.cnodeType=="1"){
          this.changeNode();
        }
        if(this.cnodeType=="2"){//
          if(this.inputValueEdit.length>10) return layer.msg("10",{icon:6})
          this.newNode();
        }

        this.highLight = true
      }
    },
    newNode(){//-
        let _self = this

        let Url = `/ei-web/attaItemAdd?itemName=${this.inputValueEdit}&fundId=${this.node_itemFundId}&parentId=${this.node_itemParentId}&itemType=3`
        axios({
          url:Url,
          method: "post"
        }).then(function(response) {
          if (response.data.status === "0" ) {
            _self.updataTree()
            _self.nodeID = ''
            _self.inputValueEdit = ''
            const msg = response.data.msg
            layer.msg(msg,{icon:6})

          }
        })
    },
    changeNode(){//
      let _self = this
      let Url = `/ei-web/attaItemUpdate/${this.node_itemParentId}?itemName=${this.inputValueEdit}`
      axios({
        url:Url,
        method: "PUT"
      }).then(function(response) {
        if (response.data.status === "0" ) {
          _self.updataTree()
          _self.nodeID = ''
          _self.inputValueEdit = ''
          const msg = response.data.msg
          layer.msg(msg,{icon:6})
        }
      })
    },
    remove(node, data) {//
      const parent = node.parent;
      const id = data.itemId
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //
          let _self = this
          let Url = `/ei-web/attaItemDelete/${id}`
          axios({
            url:Url,
            method: "DELETE"
          }).then(function(response) {
            if (response.data.status === "0" ) {
              _self.updataTree()
              layer.msg("!",{icon:6})
            }
          })

        }).catch(() => {
          layer.msg("",{icon:6})
      });


    },
    row_delete(row){

      const _self = this
      const id = row.attaId
      const itemID = this.$refs.tree.getCurrentKey()

      let Url = `/ei-web/deleteTableDataById?tableName=BD_T_FUND_ATTA&keyName=ATTA_ID&keyValue=${id}`
      axios({
        url:Url,
        method: "PUT"
      }).then(function(response) {
        if (response.data.status === "0" ) {
          const msg = response.data.msg
          layer.msg(msg,{icon:6})
          //table-data
          const pageSize    = _self.pageSize
          const currentPage = _self.currentPage
          //table-data
          _self.getTableData(itemID,pageSize,currentPage)
        }
      })
    },
    checkSelectable(row, index){//CheckBox 
      if (row.editable=='1')
        return 0;//
      else
        return 1;//
    },
    indexBtn() {// 
      this.currentPage = this.shouYePage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },
    backBtn() {// 
      this.currentPage = this.totalPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },

    prevClick(currentPage) {//
      this.currentPage = currentPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },

    nextClick(currentPage) {//
      this.currentPage = currentPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },
    handleSizeChange(size) { //pageSize 
      this.pageSize = size; //
    },
    handleCurrentChange(currentPage) {//currentPage   
      this.currentPage = currentPage; //
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    }

  }
};
</script>

